.header {
    font-size: 60px;
}

.words {
display: flex;
justify-content: center;
color: #1E88E5;
}

.word {
display: inline-block;
opacity: 0; /* Initially hidden */
transform: translateX(100px); /* Start off-screen */
animation-duration: 3s;
animation-timing-function: ease-out;
animation-fill-mode: forwards;
}

/* Left word - will animate from left */
.word.left {
animation-name: slide-in-left;
}

/* Right word - will animate from right */
.word.right {
animation-name: slide-in-right;
}

/* Keyframes for the left word (comes from left) */
@keyframes slide-in-left {
0% {
    opacity: 0;
    transform: translateX(-700px); /* Start from off-screen on the left */
}
100% {
    opacity: 1;
    transform: translateX(0); /* End at the center */
}
}

/* Keyframes for the right word (comes from right) */
@keyframes slide-in-right {
0% {
    opacity: 0;
    transform: translateX(700px); /* Start from off-screen on the right */
}
100% {
    opacity: 1;
    transform: translateX(0); /* End at the center */
}
}
